import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatePicker from "react-datepicker";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function InstituteUpdate() {
  const { register, handleSubmit, errors } = useForm();

  const { id, package_id } = useParams();
  //GET PACKAGE DATA -START
  const [pkgData, setPkgData] = useState({});
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");

      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/package-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { package_id: package_id },
      };

      const pkgApiResp = await trackPromise(axios(config));
      //   console.log("pkgApiResp - " + JSON.stringify(pkgApiResp.data.msg.params));
      setPkgData(pkgApiResp.data.msg);
      setSelectedOption(pkgApiResp.data.msg.display); // set initial value for radio button display
    }
    fetchData();
  }, [package_id]);
  //GET PACKAGE DATA -END

  //GET INSTITUTE DATA -START
  const [instData, setInstData] = useState({});
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");

      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/institute-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { institute_id: id },
      };

      const instApiResp = await trackPromise(axios(config));
      // console.log(
      //   "instApiResp - " +
      //     JSON.stringify(instApiResp.data.msg.subscription_s_date)
      // );
      setInstData(instApiResp.data.msg);
      setSubscription_s_date(
        new Date(instApiResp.data.msg.subscription_s_date)
      );
      setSubscription_e_date(
        new Date(instApiResp.data.msg.subscription_e_date)
      );
    }
    fetchData();
  }, [id]);
  //GET INSTITUTE DATA -END

  let history = useHistory();
  // UPDATE PACKAGE DATA -START
  const onSubmit = (data) => {
    if (subscription_s_date) {
      if (subscription_e_date) {
        // console.log("Form Data: " + JSON.stringify(data));

        //seperate package vlaues from data object start
        const packageData = {
          id: package_id,
          title: data.packageTitle,
          description: data.packageDescription,
          display: data.display,
        };
        // console.log("Package Data: " + JSON.stringify(packageData));
        //seperate package vlaues from data object end

        updatePkgData(packageData);
        async function updatePkgData(packageData) {
          let accessToken = localStorage.getItem("token");
          //alert(accessToken);
          accessToken = accessToken.substring(1, accessToken.length - 1);
          var config = {
            method: "post",
            url: "/package-update",
            headers: {
              Authorization: `Bearer ${accessToken}`,
              "Content-Type": "application/json",
            },
            data: packageData,
          };
          const updatePkgApiResp = await trackPromise(axios(config));
          //console.log("updatePkgApiResp - " + updatePkgApiResp.data);
          if (updatePkgApiResp.data.status === "ok") {
            //console.log("Package Update Successfully");
            // toast("Package Update Successfully", {
            //   className: "toast-error",
            // });
            // history.push("/package-list-view");
          } else {
            toast(updatePkgApiResp.data.msg, {
              className: "toast-error",
            });
          }
        }

        //seperate institute vlaues from data object start
        const instituteData = {
          id: id,
          package_id: package_id,
          institute_id: data.institute_id,
          password: data.password,
          name: data.instName,
          address: data.address,
          email: data.email,
          phone: data.phone,
          subscription_s_date: subscription_s_date,
          subscription_e_date: subscription_e_date,
          last_payment: last_payment_date,
          payment_amount: data.payment_amount,
        };
        // console.log("Institute Data: " + JSON.stringify(instituteData));
        //seperate institute vlaues from data object end
        postInstData(instituteData);
        async function postInstData(instituteData) {
          let accessToken = localStorage.getItem("token");
          //alert(accessToken);
          accessToken = accessToken.substring(1, accessToken.length - 1);
          var config = {
            method: "post",
            url: "/institute-update",
            headers: {
              Authorization: `Bearer ${accessToken}`,
              "Content-Type": "application/json",
            },
            data: instituteData,
          };
          const updateInstApiResp = await trackPromise(axios(config));
          //console.log("updateInstApiResp - " + updateInstApiResp.data);
          if (updateInstApiResp.data.status === "ok") {
            toast("Institute Information Updated Successfully", {
              className: "toast-error",
            });
            history.push("/institute-list-view");
          } else {
            toast(updateInstApiResp.data.msg, {
              className: "toast-error",
            });
          }
        }
      } else {
        setErrors_subscription_e_date(true);
      }
    } else {
      setErrors_subscription_s_date(true);
    }
  };
  // UPDATE PACKAGE DATA -END

  const [selectedOption, setSelectedOption] = useState("");
  // handle radio change
  const handleRadioChange = (e) => {
    setSelectedOption(e.target.value);
  };

  /* PACKAGE PARAMETER START*/
  const [pkgParamList, setPkgParamList] = useState([
    { name: "", price: "", quantity: "" },
  ]);

  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...pkgParamList];
    list[index][name] = value;
    setPkgParamList(list);
  };

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...pkgParamList];
    list.splice(index, 1);
    setPkgParamList(list);
  };
  /* PACKAGE PARAMETER END*/

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/

  //date start-----------------
  const [subscription_s_date, setSubscription_s_date] = useState("");
  const [errors_subscription_s_date, setErrors_subscription_s_date] = useState(
    false
  );
  const [subscription_e_date, setSubscription_e_date] = useState("");
  const [errors_subscription_e_date, setErrors_subscription_e_date] = useState(
    false
  );
  const [last_payment_date, setLast_payment_date] = useState("2020-11-20");
  const [errors_last_payment_date, setErrors_last_payment_date] = useState(
    false
  );

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Update Institute Information</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/institute-list-view"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          {/* ------------------------------------------------------------------ */}
                          {/* create institute form start */}
                          <form
                            className="mt-4"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span> Institute
                                  ID
                                </label>
                                <br />
                                <input
                                  type="text"
                                  readOnly
                                  className="form-control"
                                  name="institute_id"
                                  defaultValue={instData.institute_id}
                                  ref={register({ required: true })}
                                />
                                {errors.institute_id && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Password
                                </label>
                                <br />
                                <input
                                  id="password-field1"
                                  type={passwordShown ? "text" : "password"}
                                  className="form-control"
                                  readOnly
                                  name="password"
                                  defaultValue="0123456789"
                                  ref={register({
                                    required: "* This Field is Required",
                                    minLength: {
                                      value: 6,
                                      message: "* Minimum 6 Character Required",
                                    },
                                  })}
                                />

                                <span
                                  onClick={togglePasswordVisiblity}
                                  toggle="#password-field1"
                                  className={`fa fa-fw ${
                                    passwordShown ? "fa-eye-slash" : "fa-eye"
                                  } field-icon toggle-password`}
                                />
                                {errors.password && (
                                  <ErrorMsg>{errors.password.message}</ErrorMsg>
                                )}
                              </div>
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span> Institute
                                  Name
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="instName"
                                  defaultValue={instData.name}
                                  ref={register({ required: true })}
                                />
                                {errors.instName && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span> Institute
                                  Address
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  defaultValue={instData.address}
                                  name="address"
                                  ref={register({ required: true })}
                                />
                                {errors.address && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Mail Address
                                </label>
                                <br />
                                <input
                                  type="email"
                                  readOnly
                                  className="form-control"
                                  name="email"
                                  defaultValue={instData.email}
                                  ref={register({ required: true })}
                                />
                                {errors.email && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span> Institute
                                  Phone No.
                                </label>
                                <br />
                                <input
                                  type="number"
                                  min="0"
                                  className="form-control"
                                  name="phone"
                                  defaultValue={instData.phone}
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                />
                                {errors.phone && (
                                  <ErrorMsg>{errors.phone.message}</ErrorMsg>
                                )}
                              </div>
                            </div>

                            {/* Subscription Duration and Last Payment Date*/}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-12 col-lg-8 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>
                                  Subscription Duration
                                </label>
                                <br />
                                <div className="d-flex flex-sm-row flex-column">
                                  <div>
                                    <DatePicker
                                      className="form-control"
                                      selected={subscription_s_date}
                                      //defaultValue={new Date()}
                                      onChange={(date) => {
                                        setSubscription_s_date(date);
                                        setErrors_subscription_s_date(false);
                                      }}
                                      dateFormat="dd/MM/yyyy"
                                      showYearDropdown
                                      scrollableMonthYearDropdown
                                      placeholderText="dd/mm/yyyy"
                                    />
                                    {errors_subscription_s_date ? (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    ) : null}
                                  </div>
                                  <div>
                                    <p className="mt-2 mr-3 ml-3">to</p>
                                  </div>
                                  <div>
                                    <DatePicker
                                      className="form-control"
                                      selected={subscription_e_date}
                                      //defaultValue={new Date()}
                                      onChange={(date) => {
                                        setSubscription_e_date(date);
                                        setErrors_subscription_e_date(false);
                                      }}
                                      minDate={subscription_s_date}
                                      dateFormat="dd/MM/yyyy"
                                      showYearDropdown
                                      scrollableMonthYearDropdown
                                      placeholderText="dd/mm/yyyy"
                                    />
                                    {errors_subscription_e_date ? (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    ) : null}
                                  </div>
                                </div>
                              </div>

                              {/* <div className="col-sm-12 col-md-12 col-lg-4 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Last
                                  Payment Date
                                </label>
                                <br />
                                <DatePicker
                                  className="form-control"
                                  selected={last_payment_date}
                                  hidden
                                  //defaultValue={new Date()}
                                  onChange={(date) => {
                                    setLast_payment_date(date);
                                    setErrors_last_payment_date(false);
                                  }}
                                  dateFormat="dd/MM/yyyy"
                                  showYearDropdown
                                  scrollableMonthYearDropdown
                                  placeholderText="dd/mm/yyyy"
                                />
                                {errors_last_payment_date ? (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                ) : null}
                              </div> */}
                            </div>
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span> Payable
                                  Amount
                                </label>
                                <br />
                                <input
                                  type="number"
                                  className="form-control"
                                  name="payment_amount"
                                  defaultValue={instData.payment_amount}
                                  min="0"
                                  ref={register({ required: true })}
                                />
                                {errors.payment_amount && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <button
                              type="submit"
                              className="btn btn-update mt-2 mb-2"
                            >
                              Update
                            </button>

                            {/* PACKAGE PART START IT WILL COPY FROM EDIT PACKAGE ************************************************/}
                            {/* <div>
                              <div className="form-group row">
                                <div className="col">
                                  <label>
                                    <span className="red-star">*</span>
                                    Package Title
                                  </label>
                                  <br />
                                  <input
                                    type="text"
                                    defaultValue={pkgData.title}
                                    className="form-control"
                                    name="packageTitle"
                                    ref={register({ required: true })}
                                  />
                                  {errors.packageTitle && (
                                    <ErrorMsg>
                                      * This Field is Required
                                    </ErrorMsg>
                                  )}
                                </div>
                              </div>
                              <div className="form-group row">
                                <div className="col">
                                  <label>
                                    <span className="red-star">*</span>
                                    Package Description
                                  </label>
                                  <br />
                                  <textarea
                                    className="form-control"
                                    name="packageDescription"
                                    defaultValue={pkgData.description}
                                    ref={register({ required: true })}
                                    rows="1"
                                    cols="30"
                                  ></textarea>
                                  {errors.packageDescription && (
                                    <ErrorMsg>
                                      * This Field is Required
                                    </ErrorMsg>
                                  )}
                                </div>
                              </div>
                              <div className="form-group row">
                                <div className="col-4">
                                  <label>
                                    <span className="red-star">*</span>
                                    Package Display Front-end ?
                                  </label>
                                  <br />
                                  <div className="d-flex flex-sm-row flex-column">
                                    <div className="p-2">
                                      <input
                                        name="display"
                                        type="radio"
                                        className="mr-2"
                                        checked={selectedOption === "On"}
                                        value="On"
                                        onChange={(e) => handleRadioChange(e)}
                                        ref={register({ required: true })}
                                      />
                                      On
                                    </div>
                                    <div className="p-2">
                                      <input
                                        name="display"
                                        className="mr-2"
                                        type="radio"
                                        checked={selectedOption === "Off"}
                                        value="Off"
                                        onChange={(e) => handleRadioChange(e)}
                                        ref={register({ required: true })}
                                      />
                                      Off
                                    </div>
                                  </div>

                                  {errors.displayFrontend && (
                                    <ErrorMsg>
                                      * This Field is Required
                                    </ErrorMsg>
                                  )}
                                </div>
                              </div>
                            </div>

                            <div className="form-group">
                              <label>
                                <span className="red-star">*</span> Package
                                Parameter
                              </label>
                              <br />
                            </div>
                            {pkgData.params
                              ? pkgData.params.map((x, i) => {
                                  return (
                                    <div className="row" key={i}>
                                      <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                        <input
                                          type="text"
                                          className="form-control mb-3"
                                          defaultValue={x.name}
                                          name="name"
                                          onChange={(e) =>
                                            handleInputChange(e, i)
                                          }
                                          required
                                          ref={register({ required: true })}
                                        />
                                        {errors.name && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>

                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <input
                                          type="number"
                                          className="form-control mb-3"
                                          defaultValue={x.price}
                                          name="price"
                                          onChange={(e) =>
                                            handleInputChange(e, i)
                                          }
                                          required
                                          ref={register({ required: true })}
                                        />
                                        {errors.price && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>
                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <input
                                          type="number"
                                          className="form-control mb-3"
                                          defaultValue={x.quantity}
                                          name="quantity"
                                          onChange={(e) =>
                                            handleInputChange(e, i)
                                          }
                                          required
                                          ref={register({ required: true })}
                                        />
                                        {errors.quantity && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>
                                      <div className="col-sm-12 col-md-2 col-lg-2 col-xl-2 text-center">
                                        {i === 0 ? null : (
                                          <span
                                            onClick={() => handleRemoveClick(i)}
                                            className="shadow round-minus badge badge-bg mb-3"
                                            style={{ padding: "10px 24px" }}
                                          >
                                            <i className="fas fa-minus"></i>
                                          </span>
                                        )}
                                      </div>
                                    </div>
                                  );
                                })
                              : null} */}

                            {/* PACKAGE PART END-------------------------------------------- */}
                          </form>
                          {/* create institute form end */}
                          <LoadingIndicator />
                          {/* ----------------------------------------------------------------------- */}
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
