import "./login.css";
import React, { useState, useEffect, useRef } from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Footer from "../Layout/Footer";
import logo from "./Logo2.png";
import backgroundImage from "./bg.jpg";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function Login(props) {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  /*REMEMBER ME START*/
  const [isRememberMe, setIsRememberMe] = useState(false);
  /*REMEMBER ME END*/

  const onSubmit = (data) => {
    //console.log("Username & Password " + JSON.stringify(data));

    const email = data.email;

    trackPromise(
      axios.post("/authenticate", data).then((response) => {
        // console.log(JSON.stringify(response.data.status));
        if (response.data.status === "ok" || response.data.status === 200) {
          localStorage.setItem(
            "token",
            JSON.stringify(response.data.msg.token)
          );
          localStorage.setItem("role", JSON.stringify(response.data.msg.role));
          if (response.data.msg.role_details) {
            localStorage.setItem(
              "role_details",
              JSON.stringify(response.data.msg.role_details)
            );
          }
          localStorage.setItem("emailAddress", email);
          // console.log("email: " + email);
          // localStorage.setItem("email", "email");
          //console.log(this.props.history.push("/Dashboard"));
          if (isRememberMe) {
            localStorage.setItem("email", data.email);
            localStorage.setItem("password", data.password);
          } else {
            localStorage.removeItem("email");
            localStorage.removeItem("password");
          }
          props.history.push("/dashboard");
        } else {
          toast("Wrong Email Or Password", {
            className: "toast-error",
          });
        }
      })
    );
  };

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/

  return (
    <>
      <div className="row" style={{ minHeight: "100vh", height: "100vh" }}>
        <div className="col-4 mt-5">
          {/* logo */}
          <div className="text-center mt-5">
            <img
              src={logo}
              alt="Babylon Logo"
              className="logo-babylon-login img-fluid"
              height="50"
              width="150"
            />
          </div>
          <div className="mr-4 ml-4">
            <form className="" onSubmit={handleSubmit(onSubmit)}>
              {/* user name */}
              <div className="mb-3">
                <label>User Name</label>
                <input
                  type="email"
                  className="form-control"
                  name="email"
                  defaultValue={
                    localStorage.getItem("email")
                      ? localStorage.getItem("email")
                      : ""
                  }
                  placeholder="Enter Your Email Address"
                  ref={register({ required: true })}
                />
                {errors.email && <ErrorMsg>* This Field is Required</ErrorMsg>}
              </div>
              {/* password */}
              <div className="mb-3">
                <label>Password</label>
                <input
                  id="password-field1"
                  type={passwordShown ? "text" : "password"}
                  className="form-control"
                  name="password"
                  defaultValue={
                    localStorage.getItem("password")
                      ? localStorage.getItem("password")
                      : ""
                  }
                  placeholder="Enter Your Password"
                  ref={register({ required: true })}
                />
                <span
                  onClick={togglePasswordVisiblity}
                  toggle="#password-field1"
                  className={`fa fa-fw ${
                    passwordShown ? "fa-eye-slash" : "fa-eye"
                  } field-icon toggle-password`}
                ></span>
                {errors.password && (
                  <ErrorMsg>* This Field is Required</ErrorMsg>
                )}
              </div>
              {/* remember me and forgot password */}
              <div className="form-check">
                <div className="float-left">
                  <input
                    type="checkbox"
                    className="form-check-input check-input"
                    id="exampleCheck1"
                    checked={isRememberMe}
                    onChange={() => setIsRememberMe(!isRememberMe)}
                  />
                  <label className="form-check-label" htmlFor="exampleCheck1">
                    Remember Me
                  </label>
                </div>
                <div className="float-right">
                  <Link to="/forget-password" className="small">
                    <label
                      className="form-check-label"
                      htmlFor="exampleCheck1"
                      style={{ fontSize: "15px" }}
                    >
                      Forgot Password?
                    </label>
                  </Link>
                </div>
              </div>

              <div className="input-group mb-5">
                <button
                  className="btn bg-color shadow text-white float-left mt-4 pr-5 pl-5"
                  type="submit"
                >
                  Login
                </button>
              </div>
            </form>
          </div>
          <div className="login-footer-container mr-4 ml-4">
            <div className="login-footer">
              <Footer />
            </div>
          </div>
        </div>

        {/* right side background image */}
        <div className="col-8">
          <div
            className="login-container"
            style={{
              backgroundImage: "url(" + backgroundImage + ")",
              backgroundRepeat: "no-repeat",
              backgroundSize: "100%",
              backgroundSize: "cover",
              backgroundPosition: "center center",
              position: "relative",
              width: "100%",
              height: "100%",
            }}
          >
            <div className="site_title">
              <h1 className="pt-5">Welcome to Learnerscafe LMS Software</h1>
              <p className="login-para">
                <b>ADMIN</b>
                <br />
                The administrator is a role that is peculiar to the LMS. It is
                at the top of the hierarchy and is the only function with full
                power in relation to the entire platform. On the contrary this
                role, which is held by one or very little people, is in charge
                of all the administrative matters related to the academic
                process, just like the administrative office of an institute.
                Among the core functionalities of the Admin we can find: enroll
                new users to the platform (in all the different ways), assigning
                roles, and associate teachers to courses, enroll students in
                academic courses. As we said, this role is the only one to have
                a vision of the whole LMS, both users and uploaded content, but
                in fact has no interest in the details of single courses, which
                are managed directly by the teacher.
              </p>
              <p className="login-para">
                <b>TEACHER</b>
                <br />
                The structure and the role hierarchy within the LMS allow the
                teacher to only care about training: from the creation of the
                academic process in his course, to the choice of strategies and
                content delivery. Within his courses of competence, in fact, the
                teacher has full power in relation to the creation of lessons,
                which can be also organized according to prerequisites criteria,
                and addition of different types of activity and resources, both
                synchronous and asynchronous. In addition, the teacher has
                access to the data of his class; in particular progresses and
                results reports, and can easily share information and events in
                the course calendar. It is important to underline, in relation
                to this role, that the teacher can only see materials and
                students data of the courses he manages, while he has no
                possibility to access other courses in the Academy, unless he is
                enrolled on that course.
              </p>
              <p className="login-para">
                <b>STUDENT</b>
                <br />
                Eventually, at the base of the hierarchical pyramid of the LMS
                there are users enrolled as students. It is, obviously, the role
                with less power: it allows, in fact, to only see courses you are
                enrolled in, see events in the shared calendar and messages from
                the teacher, access reports of your own progresses and results.
                The student is the one with less active functionalities, since
                he can only complete actions and tasks if the teacher assigned
                them to him.
              </p>
            </div>
            <div className="overlay"></div>
          </div>
        </div>
      </div>
      <LoadingIndicator />
    </>
  );
}
