import React from "react";
import { HorizontalBar, Bar, Doughnut } from "react-chartjs-2";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from "recharts";
import AttendanceImg from "./attendance.png";
import LiveClassImg from "./liveClass.png";
import CalenderImg from "./calender.png";
import Asset11Img from "./Asset11.png";
import Asset12Img from "./Asset12.png";
import LiveClass1Img from "./LiveClass_1.png";
import LiveClass2Img from "./LiveClass_2.png";
import LiveClass3Img from "./LiveClass_3.png";
import LiveClass4Img from "./LiveClass_4.png";

export default function Dashboard() {
  /**************Institute chart start***************** */
  const instituteData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/
    const tot_institute_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    tot_institute_gradient.addColorStop(0, "#ff2260");
    tot_institute_gradient.addColorStop(1, "#ff736b");

    const university_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    university_gradient.addColorStop(0, "#553eff");
    university_gradient.addColorStop(1, "#00f0ff");

    const college_gradient = ctx.createLinearGradient(0, 0, 270, 0);
    college_gradient.addColorStop(0, "#0087ff");
    college_gradient.addColorStop(1, "#00e09f");

    const school_gradient = ctx.createLinearGradient(0, 0, 370, 0);
    school_gradient.addColorStop(0, "#423eff");
    school_gradient.addColorStop(1, "#fe3eff");
    /***************/

    return {
      labels: ["Total Institute", "University", "College", "School"],
      datasets: [
        {
          label: "# of Institute",
          data: [100, 60, 20, 20, 0, 100],
          backgroundColor: [
            tot_institute_gradient,
            university_gradient,
            college_gradient,
            school_gradient,
          ],
        },
      ],
    };
  };

  var instituteOptions = {
    maintainAspectRatio: false,
    legend: {
      display: false,
    },
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };
  /**************Institute chart end***************** */

  /**************Payment chart start***************** */
  const paymentData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/

    const invoice_send_gradient = ctx.createLinearGradient(0, 0, 750, 0);
    invoice_send_gradient.addColorStop(0, "#553eff");
    invoice_send_gradient.addColorStop(1, "#00f0ff");

    const payment_received_gradient = ctx.createLinearGradient(0, 0, 550, 0);
    payment_received_gradient.addColorStop(0, "#ff2260");
    payment_received_gradient.addColorStop(1, "#ff736b");

    const due_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    due_gradient.addColorStop(0, "#423eff");
    due_gradient.addColorStop(1, "#fe3eff");
    /***************/

    return {
      labels: ["Invoice Send", "Payment Received", "Due"],
      datasets: [
        {
          label: "Total",
          data: [100, 60, 40, 0, 100],
          backgroundColor: [
            invoice_send_gradient,
            payment_received_gradient,
            due_gradient,
          ],
        },
      ],
    };
  };

  var paymentOptions = {
    maintainAspectRatio: false,
    legend: {
      display: false,
    },
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };
  /**************Payment chart end***************** */

  /* /****************bandwidth chart stard**********************/

  const BandWidthData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/

    const invoice_send_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    invoice_send_gradient.addColorStop(0, "#3da9f0");
    invoice_send_gradient.addColorStop(1, "#3da9f0");

    const payment_received_gradient = ctx.createLinearGradient(0, 0, 550, 0);
    payment_received_gradient.addColorStop(0, "#3da9f0");
    payment_received_gradient.addColorStop(1, "#3da9f0");

    const due_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    due_gradient.addColorStop(0, "#3da9f0");
    due_gradient.addColorStop(1, "#3da9f0");

    /***************/

    return {
      labels: ["1", "2", "3", "4", "5", "6"],
      datasets: [
        {
          data: [12, 19, 3, 5, 2, 3],
          backgroundColor: invoice_send_gradient,
        },
        {
          data: [2, 3, 20, 5, 1, 4],
          backgroundColor: payment_received_gradient,
        },
        {
          data: [3, 10, 13, 15, 22, 30],
          backgroundColor: due_gradient,
        },
      ],
    };
  };

  const BandWidthOptions = {
    maintainAspectRatio: false,
    legend: {
      display: false,
    },
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };

  /****************bandwidth chart end**********************/

  /****************storage chart start**********************/

  const StorageData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/

    const invoice_send_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    invoice_send_gradient.addColorStop(0, "#fe3eff");
    invoice_send_gradient.addColorStop(1, "#00f0ff");

    const payment_received_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    payment_received_gradient.addColorStop(0, "#ff2260");
    payment_received_gradient.addColorStop(1, "#ff736b");

    const due_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    due_gradient.addColorStop(0, "#423eff");
    due_gradient.addColorStop(1, "#fe3eff");
    /***************/

    return {
      labels: ["Video", "Audio", "Documents", "Free"],
      datasets: [
        {
          data: [15, 12, 16, 5],
          backgroundColor: [
            invoice_send_gradient,
            payment_received_gradient,
            due_gradient,
            "#BCBEC0",
          ],
        },
      ],
    };
  };

  const StorageOptions = {
    maintainAspectRatio: false,
    legend: {
      display: true,
      position: "bottom",
      labels: {
        fontSize: 8,
        boxWidth: 8,
      },
    },
    maintainAspectRatio: false,
    responsive: true,
    cutoutPercentage: 50,
  };
  /****************storage chart end**********************/

  // new chart start
  const data = [
    { name: "", uv: 2000, pv: 2400, amt: 2400 },
    { name: "", uv: 6000, pv: 1398, amt: 2210 },
    { name: "", uv: 2000, pv: 9800, amt: 2290 },
    { name: "", uv: 4000, pv: 9800, amt: 2290 },
  ];

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3">
          <div className="content">
            <div className="container-fluid layout-width">
              <div className="row">
                {/* institute  */}
                <div className="col-sm-12 col-md-12 col-lg-5 col-xl-5">
                  <div className="custom-info-box">
                    <div className="container-chart">
                      <img
                        src={AttendanceImg}
                        className="mr-5 top-left-icon"
                        height="60"
                        width="60"
                      />
                      <span className="top-left-text">Institute</span>
                    </div>
                    <div className="mt-3 pt-5">
                      <HorizontalBar
                        data={instituteData}
                        options={instituteOptions}
                        height={200}
                        width={400}
                      />
                    </div>
                    {/* Institute chart end */}
                  </div>
                </div>
                {/* Payment */}
                <div className="col-sm-12 col-md-12 col-lg-5 col-xl-5">
                  <div className="custom-info-box">
                    <div className="container-chart">
                      <img
                        src={AttendanceImg}
                        className="mr-5 top-left-icon"
                        height="60"
                        width="60"
                      />
                      <span className="top-left-text">Payment</span>
                    </div>
                    <div className="mt-3 pt-5">
                      <HorizontalBar
                        data={paymentData}
                        options={paymentOptions}
                        height={200}
                        width={400}
                      />
                    </div>
                  </div>
                </div>
                {/* System Info */}
                <div className="col-sm-12 col-md-12 col-lg-2 col-xl-2">
                  {/* text sytem info */}
                  <div className="system-info-box mt-3 mb-2">
                    <div className="container-chart">
                      <img
                        src={AttendanceImg}
                        className="mr-5 top-left-icon"
                        height="60"
                        width="60"
                      />
                      <span className="top-left-text system-info-text">
                        System Info
                      </span>
                    </div>
                  </div>
                  {/* Storage */}
                  <div className="storage-info-box">
                    <div>
                      <span className="system-info-chart-text">Storage</span>
                      <div className="mb-4 mt-1">
                        <Doughnut
                          data={StorageData}
                          width={100}
                          height={150}
                          options={StorageOptions}
                        />
                      </div>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                </div>
              </div>
              <div className="row mt-4">
                {/* live class */}
                <div className="col-sm-12 col-md-12 col-xl-6 col-xl-6 mt-2 mb-2">
                  <div className="live-class-box">
                    <div className="container-chart">
                      <img
                        src={LiveClassImg}
                        className="mr-5 top-left-icon"
                        height="60"
                        width="60"
                      />
                      <span className="top-left-text">Live Class</span>
                    </div>
                    {/* Live class chart start */}
                    <div className="container-chart">
                      <img
                        src={LiveClass1Img}
                        className="img-fluid ml-2 mr-4 current-class-img"
                        width="100"
                      />
                      <span className="current-class-text">Current Class</span>
                      <span className="current-class-num">7</span>
                      <img
                        src={LiveClass2Img}
                        className="img-fluid mr-4 this-week-img"
                        width="100"
                      />
                      <span className="this-week-text">This Week</span>
                      <span className="this-week-num">56</span>
                      <img
                        src={LiveClass3Img}
                        className="img-fluid mr-4 this-month-img"
                        width="100"
                      />
                      <span className="this-month-text">This Month</span>
                      <span className="this-month-num">100</span>
                      <img
                        src={LiveClass4Img}
                        className="img-fluid mr-2 last-month-img"
                        width="100"
                      />
                      <span className="last-month-text">Last Month</span>
                      <span className="last-month-num">220</span>
                    </div>
                  </div>
                </div>
                {/* New User */}
                <div className="col-sm-12 col-md-12 col-xl-4 col-xl-4 mt-2 mb-2">
                  <div className="new-user-box">
                    <div className="container-chart">
                      <img
                        src={CalenderImg}
                        className="mr-5 top-left-icon"
                        height="60"
                        width="60"
                      />
                      <span className="top-left-text">New User</span>
                      {/* New User chart start */}
                      <div className="container-chart">
                        <img
                          src={Asset11Img}
                          className="img-fluid ml-2 mr-4 current-img"
                          width="100"
                        />
                        <span className="current-text">Current</span>
                        <span className="current-number">450</span>
                        <img
                          src={Asset12Img}
                          className="img-fluid mr-2 week-img"
                          width="100"
                        />
                        <span className="week-text">This Week</span>
                        <span className="week-number">94</span>
                      </div>
                      {/* New User chart end */}
                    </div>
                    {/* /.info-box-content */}
                  </div>
                </div>
                {/* Bandwidth usages */}
                <div className="col-sm-12 col-md-12 col-xl-2 col-xl-2 mt-2">
                  <div className="live-class-box">
                    <div className="container-chart">
                      <span className="system-info-chart-text">
                        Bandwidth usages
                      </span>
                      <div className="mb-3 mt-2">
                        <Bar
                          data={BandWidthData}
                          options={BandWidthOptions}
                          width={100}
                          height={150}
                        />
                      </div>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                </div>
              </div>
              <div className="row">
                {/* Churn Rate Details */}
                <div className="col-sm-12 col-md-12 col-lg-12 col-xl-10 mt-1">
                  <div className="table-info-box">
                    <div className="container-chart">
                      <img
                        src={AttendanceImg}
                        className="mr-5 top-left-icon"
                        height="60"
                        width="60"
                      />
                      <span className="top-left-text">Churn Rate Details</span>
                    </div>
                    <div className="mt-3 pt-5">
                      <div className="table-responsive">
                        <table className="table table-striped table-sm">
                          <thead>
                            <tr>
                              <th></th>
                              <th className="text-left">August</th>
                              <th className="text-left">September</th>
                              <th className="text-left">October</th>
                              <th className="text-left">November</th>
                              <th className="text-left">December</th>
                              <th className="text-left">January</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td className="text-left pl-5">
                                Existing Customers
                              </td>
                              <td className="text-left">10,000</td>
                              <td className="text-left">14,000</td>
                              <td className="text-left">18,531</td>
                              <td className="text-left">18,531</td>
                              <td className="text-left">18,531</td>
                              <td className="text-left">18,531</td>
                            </tr>
                            <tr>
                              <td className="text-left pl-5">Existing Churn</td>
                              <td className="text-left">500</td>
                              <td className="text-left">719</td>
                              <td className="text-left">927</td>
                              <td className="text-left">927</td>
                              <td className="text-left">927</td>
                              <td className="text-left">927</td>
                            </tr>
                            <tr>
                              <td className="text-left pl-5">New Customers</td>
                              <td className="text-left">5000</td>
                              <td className="text-left">5000</td>
                              <td className="text-left">5000</td>
                              <td className="text-left">5000</td>
                              <td className="text-left">5000</td>
                              <td className="text-left">5000</td>
                            </tr>
                            <tr>
                              <td className="text-left pl-5">New Churn</td>
                              <td className="text-left">125</td>
                              <td className="text-left">125</td>
                              <td className="text-left">125</td>
                              <td className="text-left">125</td>
                              <td className="text-left">125</td>
                              <td className="text-left">125</td>
                            </tr>
                            <tr>
                              <td className="text-left pl-5">
                                Total Customers
                              </td>
                              <td className="text-left">14,375</td>
                              <td className="text-left">18,531</td>
                              <td className="text-left">22,479</td>
                              <td className="text-left">22,479</td>
                              <td className="text-left">22,479</td>
                              <td className="text-left">22,479</td>
                            </tr>
                            <tr>
                              <td className="text-left pl-5">
                                Adjusted Churn Rate
                              </td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                            </tr>
                            <tr>
                              <td className="text-left pl-5">
                                Quartely Churn Rate
                              </td>
                              <td className="text-left">5.17%</td>
                              <td className="text-left">5.17%</td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                              <td className="text-left">5.13%</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
                {/* ram and processor */}
                <div className="col-sm-12 col-md-12 col-lg-12 col-xl-2 mt-3">
                  <div className="info-box">
                    <div className="info-box-content attendance-thumbnail">
                      <span className="system-info-chart-text">Ram usages</span>
                      <div className="mb-3 mt-5">
                        <AreaChart
                          width={150}
                          height={100}
                          data={data}
                          margin={{
                            top: 10,
                            right: 30,
                            left: 0,
                            bottom: 0,
                          }}
                        >
                          <CartesianGrid strokeDasharray="3 3" />
                          <XAxis dataKey="name" />
                          <YAxis />
                          <Tooltip />
                          <Area
                            type="monotone"
                            dataKey="uv"
                            stroke="#465FFF"
                            fill="#2F8FFF"
                          />
                        </AreaChart>
                      </div>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* -------------------- */}
                  <div className="info-box">
                    <div className="info-box-content attendance-thumbnail">
                      <span className="system-info-chart-text">
                        Processor usages
                      </span>
                      <div className="mb-3 mt-5">
                        <AreaChart
                          width={150}
                          height={100}
                          data={data}
                          margin={{
                            top: 10,
                            right: 30,
                            left: 0,
                            bottom: 0,
                          }}
                        >
                          <CartesianGrid strokeDasharray="3 3" />
                          <XAxis dataKey="name" />
                          <YAxis />
                          <Tooltip />
                          <Area
                            type="monotone"
                            dataKey="uv"
                            stroke="#465FFF"
                            fill="#FF2260"
                          />
                        </AreaChart>
                      </div>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
