import React, { useState, useEffect, useContext } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import { ProfileContext } from "../../ProfileContext";
import Moment from "moment";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function UpdateProfile() {
  const [isProfileUpdate, setIsProfileUpdate] = useContext(ProfileContext);

  let accessToken = localStorage.getItem("token");
  let email = localStorage.getItem("emailAddress");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  let history = useHistory();
  const onSubmit = (data) => {
    if (selectedDate) {
      if (selectedDoj) {
        if (!errorDateInvalid) {
          //console.log(data);

          data = {
            ...data,
            date_of_birth: selectedDate,
            date_of_joining: selectedDoj,
            email: email,
          };
          //console.log("Data: " + JSON.stringify(data));
          var config = {
            method: "post",
            url: "/info-update",
            headers: {
              Authorization: `Bearer ${accessToken}`,
            },
            data,
          };

          async function fetchData() {
            const userApiResp = await trackPromise(axios(config));
            //console.log(JSON.stringify(userApiResp.data));
            if (userApiResp.data.status === "ok") {
              toast("Profile Info Updated Successfully", {
                className: "toast-error",
              });
              setIsProfileUpdate(true);
            } else {
              toast(userApiResp.data.msg, {
                className: "toast-error",
              });
            }
          }
          fetchData();
        } else {
          return;
        }
      } else {
        setErrorDoj(true);
      }
    } else {
      setErrors_date_of_birth(true);
    }
  };

  ///api for get user info--------------

  const [user, setUser] = useState({});

  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      let email = localStorage.getItem("emailAddress");
      var config = {
        method: "post",
        url: "/user-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          email: email,
        },
      };

      // test
      let response = await trackPromise(axios(config));
      if (response.data.status === "ok") {
        //console.log(response.data.msg);
        setSelectedDate(new Date(response.data.msg.date_of_birth));
        setSelectedDoj(new Date(response.data.msg.date_of_joining));
        setSelectedGender(response.data.msg.gender);
        setSelectedMaritalStatus(response.data.msg.marital_status);
        setSelectedReligion(response.data.msg.religion);
        setUser(response.data.msg);
      }
      if (isProfileUpdate) {
        history.push("/view-profile");
      }
    }
    fetchData();
  }, [isProfileUpdate]);
  //console.log(user.date_of_birth);
  //

  const [selectedDate, setSelectedDate] = useState("");
  const [selectedDoj, setSelectedDoj] = useState("");
  const [selectedGender, setSelectedGender] = useState("");
  const [selectedMaritalStatus, setSelectedMaritalStatus] = useState("");
  const [selectedReligion, setSelectedReligion] = useState("");
  const [errors_date_of_birth, setErrors_date_of_birth] = useState(false);
  const [errorDoj, setErrorDoj] = useState(false);
  const [errorDateInvalid, setErrorDateInvalid] = useState(false);

  function checkDateIsEmpty() {
    //console.log("selectedDate : " + selectedDate);
    if (selectedDate === "") {
      setErrors_date_of_birth(true);
    }
    if (selectedDoj === "") {
      setErrorDoj(true);
    }
  }
  function checkDate() {
    // console.log(
    //   "selectedDoj: " + selectedDoj + "-selectedDate: " + selectedDate
    // );
    if (selectedDoj && selectedDate) {
      if (selectedDoj <= selectedDate) {
        setErrorDateInvalid(true);
      } else {
        setErrorDateInvalid(false);
      }
    }
  }
  return (
    <>
      {/* edit form general info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        {/* First Name and Last Name */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> First Name
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="fname"
              defaultValue={user.fname}
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^[a-zA-Z&.: ]+$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.fname && <ErrorMsg>{errors.fname.message}</ErrorMsg>}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Last Name</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="lname"
              defaultValue={user.lname}
              ref={register({
                pattern: {
                  value: /^[a-z\s]{0,255}$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.lname && <ErrorMsg>{errors.lname.message}</ErrorMsg>}
          </div>
        </div>
        {/* Date of Birth and Gender */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> Date of Birth
            </label>
            <br />
            <DatePicker
              className="form-control"
              selected={selectedDate}
              onChange={(date) => {
                setSelectedDate(date);
                setErrors_date_of_birth(false);
              }}
              dateFormat="dd-MM-yyyy"
              maxDate={new Date()}
              showYearDropdown
              scrollableMonthYearDropdown
            />
            {errors_date_of_birth && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> Gender
            </label>
            <br />
            <select
              className="form-control"
              name="gender"
              ref={register({ required: true })}
              value={selectedGender}
              onChange={(e) => setSelectedGender(e.target.value)}
            >
              <option hidden value="">
                ---Select---
              </option>
              <option value="Male">Male</option>
              <option value="Female">Female</option>
              <option value="Other">Other</option>
            </select>
            {errors.gender && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* Marital Status and Religion */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Marital Status</label>
            <br />
            <select
              className="form-control"
              name="marital_status"
              ref={register}
              value={selectedMaritalStatus}
              onChange={(e) => setSelectedMaritalStatus(e.target.value)}
            >
              <option hidden value="">
                ---Select---
              </option>
              <option value="Single">Single</option>
              <option value="Married">Married</option>
              <option value="Divorced">Divorced</option>
              <option value="Widowed">Widowed</option>
              <option value="Other">Other</option>
            </select>
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Religion</label>
            <br />
            <select
              className="form-control"
              name="religion"
              defaultValue={user.religion}
              ref={register}
              value={selectedReligion}
              onChange={(e) => setSelectedReligion(e.target.value)}
            >
              <option hidden value="">
                ---Select---
              </option>
              <option value="Islam">Islam</option>
              <option value="Hinduism">Hinduism</option>
              <option value="Buddhism">Buddhism</option>
              <option value="Christianity">Christianity</option>
              <option value="Other">Other</option>
            </select>
          </div>
        </div>
        {/* Date of Joining and employee id */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Date of Joining</label>
            <br />
            <DatePicker
              className="form-control"
              selected={selectedDoj}
              onChange={(date) => {
                setSelectedDoj(date);
                setErrorDoj(false);
              }}
              dateFormat="dd-MM-yyyy"
              placeholderText="dd-mm-yyyy"
              showYearDropdown
              scrollableMonthYearDropdown
            />
            {errorDoj ? <ErrorMsg>* This Field is Required</ErrorMsg> : null}
            {errorDateInvalid ? (
              <ErrorMsg style={{ marginLeft: "8px" }}>
                * Date of joining should be greater than date of birth
              </ErrorMsg>
            ) : null}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Employee ID</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="employee_id"
              defaultValue={user.employee_id}
              ref={register({
                required: "* This Field is Required",
              })}
            />
            {errors.employee_id && (
              <ErrorMsg>{errors.employee_id.message}</ErrorMsg>
            )}
          </div>
        </div>
        {/* Mobile No. and NID */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> Mobile No.
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="mobile"
              defaultValue={user.mobile}
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^([+]\d+)?\d+$/, //only +0121321 or 123564
                  message: "* Please Enter Valid Number",
                },
              })}
            />
            {errors.mobile && <ErrorMsg>{errors.mobile.message}</ErrorMsg>}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>NID</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="nid"
              defaultValue={user.nid}
              ref={register({
                pattern: {
                  value: /^[0-9]\d*$/, //only positive number allow
                  message: "* Please Enter Positive Number",
                },
              })}
            />
            {errors.nid && <ErrorMsg>{errors.nid.message}</ErrorMsg>}
          </div>
        </div>
        {/* Nationality and Designation */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Nationality</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="nationality"
              defaultValue={user.nationality}
              ref={register}
            />
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Designation</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="designation"
              defaultValue={user.designation}
              ref={register}
            />
          </div>
        </div>
        {/* Place of Birth */}
        <div className="form-group row">
          <div className="col">
            <label>Place of Birth</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="place_of_birth"
              defaultValue={user.place_of_birth}
              ref={register}
            />
          </div>
        </div>
        <button
          type="submit"
          className="btn btn-update"
          onClick={() => {
            checkDateIsEmpty();
            checkDate();
          }}
        >
          Save
        </button>
      </form>
      {/* edit form general info end */}
      <LoadingIndicator />
    </>
  );
}
