import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatePicker from "react-datepicker";
import Moment from "moment";
import "react-datepicker/dist/react-datepicker.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function CreateUserForm() {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  let accessToken = localStorage.getItem("token");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  let history = useHistory();
  const onSubmit = (data) => {
    if (selectedDate) {
      if (selectedDoj) {
        if (data.confirmPassword === data.password) {
          if (!errorDateInvalid) {
            //console.log("Data: " + JSON.stringify(data));
            //console.log("selectedDate: " + selectedDate);
            data = {
              ...data,
              date_of_birth: selectedDate,
              date_of_joining: selectedDoj,
              username: data.email,
            };
            delete data["confirmPassword"];
            //console.log("Data: " + JSON.stringify(data));
            var config = {
              method: "post",
              url: "/RegisterNewUserNormal",
              headers: {
                Authorization: `Bearer ${accessToken}`,
              },
              data: data,
            };

            trackPromise(
              axios(config).then(function (response) {
                // console.log(JSON.stringify(response.data));
                if (response.data.status === "ok") {
                  toast("User Created Successfully", {
                    className: "toast-error",
                  });
                  history.push("/user-list-view");
                } else {
                  toast(response.data.msg, {
                    className: "toast-error",
                  });
                }
              })
            );
          } else {
            return;
          }
        } else {
          setErrors_two_password(true);
        }
      } else {
        setErrorDoj(true);
      }
    } else {
      setErrors_date_of_birth(true);
    }
  };

  const [data, setData] = useState([]);
  const [role, setRole] = useState("");

  //GET USERTYPE -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetUserTypeList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const userTypeApiResp = await trackPromise(axios(config));
      // console.log(
      //   "userTypeApiResp - " + JSON.stringify(userTypeApiResp.data.data)
      // );
      let apiResponse = userTypeApiResp.data.data;
      const apiResponseFiltered = apiResponse.filter(
        (res) => res.active === "1"
      );

      setData(apiResponseFiltered);
      setRole(userTypeApiResp.data.data.typename);
    }
    fetchData();
  }, []);
  //GET USERTYPE -END

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const [confirmPasswordShown, setConfirmPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };

  const toggleConfirmPasswordVisiblity = () => {
    setConfirmPasswordShown(confirmPasswordShown ? false : true);
  };

  /*SHOW OR HIDE PASSWORD END*/
  const [selectedDate, setSelectedDate] = useState("");
  const [selectedDoj, setSelectedDoj] = useState("");
  const [errors_date_of_birth, setErrors_date_of_birth] = useState(false);
  const [errorDoj, setErrorDoj] = useState(false);
  const [errorDateInvalid, setErrorDateInvalid] = useState(false);
  const [errors_two_password, setErrors_two_password] = useState(false);
  function checkDateIsEmpty() {
    //console.log("selectedDate : " + selectedDate);
    if (selectedDate === "") {
      setErrors_date_of_birth(true);
    }
    if (selectedDoj === "") {
      setErrorDoj(true);
    }
  }
  function checkDate() {
    if (selectedDoj && selectedDate) {
      if (selectedDoj <= selectedDate) {
        setErrorDateInvalid(true);
      }
    }
  }
  return (
    <div>
      {/* create form general info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        {/* firstname and lastname */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> First Name
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="fname"
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^[a-zA-Z&.: ]+$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.fname && <ErrorMsg>{errors.fname.message}</ErrorMsg>}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Last Name</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="lname"
              ref={register({
                pattern: {
                  value: /^[a-z\s]{0,255}$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.lname && <ErrorMsg>{errors.lname.message}</ErrorMsg>}
          </div>
        </div>
        {/* email and mobile no */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Email
            </label>
            <br />
            <input
              type="email"
              className="form-control"
              name="email"
              ref={register({
                required: "* This Field is Required",
              })}
            />
            {errors.email && <ErrorMsg>{errors.email.message}</ErrorMsg>}
          </div>

          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Mobile No.
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="mobile"
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^([+]\d+)?\d+$/, //only +0121321 or 123564
                  message: "* Please Enter Valid Number",
                },
              })}
            />
            {errors.mobile && <ErrorMsg>{errors.mobile.message}</ErrorMsg>}
          </div>
        </div>
        {/* password and confirm password */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Password
            </label>
            <br />
            <input
              id="password-field1"
              type={passwordShown ? "text" : "password"}
              className="form-control"
              name="password"
              ref={register({
                required: "* This Field is Required",
                minLength: {
                  value: 8,
                  message: "* Minimum 8 Character Required",
                },
              })}
            />

            <span
              onClick={togglePasswordVisiblity}
              toggle="#password-field1"
              className={`fa fa-fw ${
                passwordShown ? "fa-eye-slash" : "fa-eye"
              } field-icon toggle-password`}
            />
            {errors.password && <ErrorMsg>{errors.password.message}</ErrorMsg>}
          </div>

          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Confirm Password
            </label>
            <br />
            <input
              id="confirm-password-field1"
              type={confirmPasswordShown ? "text" : "password"}
              className="form-control"
              name="confirmPassword"
              ref={register({
                required: "* This Field is Required",
                minLength: {
                  value: 8,
                  message: "* Minimum 8 Character Required",
                },
              })}
            />

            <span
              onClick={toggleConfirmPasswordVisiblity}
              toggle="#confirm-password-field1"
              className={`fa fa-fw ${
                confirmPasswordShown ? "fa-eye-slash" : "fa-eye"
              } field-icon toggle-password`}
            />
            {errors.confirmPassword && (
              <ErrorMsg>{errors.confirmPassword.message}</ErrorMsg>
            )}
          </div>
          {errors_two_password ? (
            <ErrorMsg style={{ marginLeft: "8px" }}>
              * Password and confirm password does not match
            </ErrorMsg>
          ) : null}
        </div>
        {/* Date of Birth and gender */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Date of Birth
            </label>
            <br />
            <DatePicker
              className="form-control"
              selected={selectedDate}
              onChange={(date) => {
                setSelectedDate(date);
                setErrors_date_of_birth(false);
              }}
              dateFormat="dd-MM-yyyy"
              placeholderText="dd-mm-yyyy"
              maxDate={new Date()}
              showYearDropdown
              scrollableMonthYearDropdown
            />
            {errors_date_of_birth ? (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            ) : null}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Gender
            </label>
            <br />
            <select
              className="form-control"
              name="gender"
              ref={register({ required: true })}
            >
              <option hidden value="">
                ---Select---
              </option>
              <option value="Male">Male</option>
              <option value="Female">Female</option>
              <option value="Other">Other</option>
            </select>
            {errors.gender && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* date of joining and employee id */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Date of Joining
            </label>
            <br />
            <DatePicker
              className="form-control"
              selected={selectedDoj}
              onChange={(date) => {
                setSelectedDoj(date);
                setErrorDoj(false);
              }}
              dateFormat="dd-MM-yyyy"
              placeholderText="dd-mm-yyyy"
              showYearDropdown
              scrollableMonthYearDropdown
            />
            {errorDoj ? <ErrorMsg>* This Field is Required</ErrorMsg> : null}
            {errorDateInvalid ? (
              <ErrorMsg style={{ marginLeft: "8px" }}>
                * Date of joining should be greater than date of birth
              </ErrorMsg>
            ) : null}
          </div>

          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> Employee ID
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="employee_id"
              ref={register({
                required: "* This Field is Required",
              })}
            />
            {errors.employee_id && (
              <ErrorMsg>{errors.employee_id.message}</ErrorMsg>
            )}
          </div>
        </div>
        {/* User Type */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>User Type
            </label>
            <br />
            {data.length === 0 ? null : (
              <select
                className="form-control"
                id="role"
                name="role"
                value={role}
                onChange={(e) => setRole(e.target.value)}
                ref={register({ required: true })}
              >
                <option hidden value="">
                  ---Select User Type---
                </option>
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {data
                  .sort((a, b) => a.typename.localeCompare(b.typename))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.typename}>
                        {e.typename}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.role && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <button
          type="submit"
          className="btn btn-update"
          onClick={() => {
            checkDateIsEmpty();
            checkDate();
          }}
        >
          Create
        </button>
      </form>
      {/* create form general info end */}
      <LoadingIndicator />
    </div>
  );
}
