import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function UpdateAddress() {
  let accessToken = localStorage.getItem("token");
  let email = localStorage.getItem("emailAddress");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);
  const { register, handleSubmit, errors } = useForm(); // initialise the hook
  let history = useHistory();
  const onSubmit = (data) => {
    data["email"] = email;
    var config = {
      method: "post",
      url: "/update-user-address",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      data,
    };
    //console.log("Data: " + JSON.stringify(data));
    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast("Address info updated", {
            className: "toast-error",
          });
          history.push("/view-profile");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };

  const [user, setUser] = useState({});

  const [isSameAsPresent, setIsSameAsPresent] = useState(false);

  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      let email = localStorage.getItem("emailAddress");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/user-address",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          email: email,
        },
      };

      const userApiResp = await trackPromise(axios(config));
      //console.log("TEST user - " + userApiResp.data.msg);
      setUser(userApiResp.data.msg);
    }
    fetchData();
  }, []);

  //onchange is same present -start
  const ChangeIsSameAsPresent = (e) => {
    setIsSameAsPresent(e.target.checked);
    if (e.target.checked) {
      let address = document.querySelector("#address").value;

      setUser({
        ...user,
        per_address: address,
      });
    }
  };
  //onchange is same present -end

  return (
    <>
      {/* edit form address info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        <h5>Present Address</h5>
        {/* Address */}
        <div className="form-group row">
          <div className="col">
            <label>Address</label>
            <br />
            <textarea
              rows="5"
              id="address"
              className="form-control"
              name="address"
              defaultValue={user.address}
              ref={register({ required: true })}
            ></textarea>
            {errors.address && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <div className="d-flex flex-sm-row flex-column">
          <div className="mr-auto">
            <h5>Permanent Address</h5>
          </div>
          <div>
            {/* Same as Present Address */}
            <div className="form-check float-right">
              <input
                className="form-check-input check-input"
                type="checkbox"
                onChange={(e) => ChangeIsSameAsPresent(e)}
                checked={isSameAsPresent}
                id="defaultCheck1"
              />
              <label className="form-check-label" htmlFor="defaultCheck1">
                Same as Present Address
              </label>
            </div>
          </div>
        </div>
        {/* permanent address */}
        <div className="form-group row">
          <div className="col">
            <div className="row pb-2">
              <div className="col">
                <label>Address</label>
              </div>
            </div>
            <textarea
              rows="5"
              className="form-control"
              id="per_address"
              name="per_address"
              defaultValue={user.per_address}
              ref={register({ required: true })}
            ></textarea>
            {errors.per_address && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>

        <button type="submit" className="btn btn-update">
          Save
        </button>
      </form>
      {/* edit form address info end */}
      <LoadingIndicator />
    </>
  );
}
